import React from "react";

// components
// import Header from '../../../Components/Blogs/Header';
// import Footer from '../../../Components/Blogs/Footer';

import Header from "../components/Header";
import Footer from "../components/Footer";

// react-helmet
import { Helmet } from "react-helmet";

// styles
import "./styles/index.css";

// assets
import RichMediaSupport from "./assets/rich-media-support.png";
import Translate from "./assets/translate.png";
import AutoSalesManagement from "./assets/sales.svg";
import Support from "./assets/support.svg";

// features assets
import Call24 from "./assets/24-hours.svg";
import ChatBot from "./assets/chatbot.svg";
import Filter from "./assets/filter.svg";
import Chat from "./assets/chat.svg";
import Clock from "./assets/clock.svg";
import Verified from "./assets/twitter-verified.svg";
import SaveMoney from "./assets/save-money.svg";
import APIIcon from "./assets/coding.svg";

// experience assets
import WaOne from "./assets/whatsapp.png";
import WaTwo from "./assets/whatsapp2.png";

// video import
import ChatBotVideo from "./assets/video/chatbot.mp4";

const WhatsappChatbot = () => {
  return (
    <>
      {/* Head */}
      <Helmet>
        <title>
          World's Best WhatsApp Chat bot for sales and 24 x 7 customer support -
          EasySocial
        </title>
        <link rel="canonical" href="https://easysocial.io/whatsapp-chatbot" />
        <meta
          name="keywords"
          content="Affordable WhatsApp for business ,Whatsapp for business , Free WhatsApp for business, WhatsApp Business API "
        />
        <meta
          name="description"
          content="Create WhatsApp chat bot to sell, upsell or for customer support. Be available for your custiomers 24 hours 365 days."
        />
      </Helmet>

      {/* Header */}
      <Header />

      {/* Page Header */}
      <div className="page-header group">
        <div className="page-header-content">
          <div className="description">
            <div className="heading">
              <h1>WhatsApp Chatbot</h1>
              <p>Supercharged WhatsApp for Customer Engagement</p>
            </div>

            <p className="info">
              With the state of the art WhatsApp Business chat bot solution,
              easysocial has the ability to drive compelling two way
              conversations. When a customer has an inquiry it is fulfilled at
              the same instant. easysocial brings superfast automated
              conversations with extreme capabilities like invoice delivery,
              sending business location coordinates, sharing brochure, images
              and other rich media.
            </p>
          </div>

          {/* VIDEO */}
          <div className="header-asset">
            <video autoPlay muted loop>
              <source src={ChatBotVideo} type="video/mp4" />
            </video>
          </div>
        </div>
      </div>

      {/* Container */}
      <div className="container">
        {/* WhatsApp Info */}
        <div className="whatsapp-info group">
          <h1>Why WhatsApp?</h1>

          <div className="info-tile">
            <h1>2.2 Billion</h1>
            <p>Global Users</p>
          </div>

          <div className="info-tile">
            <h1>No. 1</h1>
            <p>Chat Platform</p>
          </div>

          <div className="info-tile">
            <div>
              <img
                src={RichMediaSupport}
                alt="Rich Media Support"
                className="tile-icon"
              />
            </div>
            <p>Rich Media Support</p>
          </div>

          <div className="info-tile">
            {/* Chinese Chat jpeg */}
            <img src={Translate} alt="Translate icon" className="tile-icon" />
            <p>Multilingual Chats</p>
          </div>
        </div>
      </div>

      {/* WhatsApp Chatbot Info */}
      <div className="whatsapp-chatbot-container">
        <div className="whatsapp-chatbot group">
          <div className="container">
            <h2>What can WhatsApp Chat Bot Enable?</h2>

            <div className="chatbot-info group">
              {/* Money Chart Img */}
              <div className="info-img">
                <img src={AutoSalesManagement} alt="Save Money icon" />
              </div>

              <div className="info-content">
                <h1>Automated Sales Management</h1>
                <p>
                  Let your intelligent WhatsApp chat bot handle pre-sales
                  enquiries and give your customers a tour of your products and
                  services. It manages multiple simultaneous enquiries and gives
                  your sales team enough time to focus only on high intent
                  queries. Send personalized messages to your customers and
                  provide them with critical information like brochures, contact
                  cards, business location, pictures, product catalogue, cart
                  reminders, offers and more to grab their instant attention.
                  With zero delay in conversations, you never lose their
                  attention and every conversation is a chance for conversion.
                </p>
              </div>
            </div>

            <div className="chatbot-info group">
              {/* Money Chart Img */}
              <div className="info-content">
                <h1>Automated Customer Service Management</h1>

                <p>
                  Experience a customer care service that never sleeps and is
                  always ready with a convincing answer. WhatsApp customer
                  service helps you manage basic customer queries through a well
                  informed chatbot that can be fed with hundreds and thousands
                  of questions and scenarios that it can respond to within
                  seconds. Apart from basic FAQ's, Whatsapp chat bot can help
                  customers with personalized account information that can be
                  fetched from a remote server and information like, account
                  balance, last 5 transactions, bill due, etc. can be provided
                  seamlessly. And yes when the customer is too demanding, the
                  chat can be automatically routed to a human representative.
                </p>
              </div>

              <div className="info-img">
                <img src={Support} alt="Support Icon" />
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Features */}
      <div className="container">
        <div className="features group">
          <h2>Features</h2>

          <div className="feature-tiles">
            <div className="feature-tile">
              <div className="feature-icon">
                <img src={Call24} alt="Feature Tile Icon" />
              </div>
              <h5>Be available 24x7</h5>
            </div>

            <div className="feature-tile">
              <div className="feature-icon">
                <img src={Chat} alt="Feature Tile Icon" />
              </div>
              <h5>Manage multiple conversations simultaneously</h5>
            </div>

            <div className="feature-tile">
              <div className="feature-icon">
                <img src={RichMediaSupport} alt="Feature Tile Icon" />
              </div>
              <h5>Share photos, videos, invoice, pdfs &amp; more...</h5>
            </div>

            <div className="feature-tile">
              <div className="feature-icon">
                <img src={ChatBot} alt="Feature Tile Icon" />
              </div>
              <h5>Personalized human like conversations</h5>
            </div>

            <div className="feature-tile">
              <div className="feature-icon" style={{ marginBottom: "35px" }}>
                <img src={Clock} alt="Feature Tile Icon" />
              </div>
              <h5>Real time enquiry quality filtration</h5>
            </div>

            <div className="feature-tile">
              <div className="feature-icon">
                <img src={SaveMoney} alt="Feature Tile Icon" />
              </div>
              <h5>Collect payments, inbuilt payment gateway.</h5>
            </div>

            <div className="feature-tile">
              <div className="feature-icon">
                <img src={Filter} alt="Feature Tile Icon" />
              </div>
              <h5>Auto filter and assign enquiries to team.</h5>
            </div>

            <div className="feature-tile">
              <div className="feature-icon">
                <img src={Verified} alt="Feature Tile Icon" />
              </div>
              <h5>Verified WhatsApp account, with blue tick</h5>
            </div>

            <div className="feature-tile">
              <div className="feature-icon">
                <img src={APIIcon} alt="Feature Tile Icon" />
              </div>
              <h5>API integration for personalized customer response</h5>
            </div>
          </div>
        </div>

        {/* Rich Experience */}
        <div className="experience">
          <h2>Rich WhatsApp Experience</h2>

          <div className="group">
            <div className="experience-info group">
              <div className="content">
                <div className="experience-header-num">1.</div>

                <h2 className="header-desc">Multi Functional Buttons</h2>

                <p>
                  Our WhatsApp ChatBot allows multi functional buttons, which
                  can be used to navigate within the chat, a page on the web or
                  even a payment gateway. As this button grabs more attention
                  the customer can be pointed to important information easily.
                </p>
              </div>

              <div className="img">
                <img src={WaOne} alt="WhatsApp Chat Page" />
              </div>
            </div>

            <div className="experience-info group">
              <div className="content">
                <div className="experience-header-num">2.</div>

                <h2 className="header-desc">Rich Multimedia Messaging</h2>

                <p>
                  WhatsApp chatbot allows you to communicate with your customers
                  in full colors and rich media. Deliver creative content to
                  grab maximum attention and provide engaging content to the
                  customer to gain his trust in your product. Rich experience
                  lead to faster conversions.
                </p>
              </div>

              <div className="img">
                <img src={WaTwo} alt="WhatsApp Chat Page" />
              </div>
            </div>
          </div>

          <h3 className="many-more">&amp; many more</h3>
        </div>
      </div>

      {/* Footer */}
      <Footer />
    </>
  );
};

export default WhatsappChatbot;
